.nav-buttons {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    top: 0;
    left: 0;
    height: 60px;
    color: rgba(255, 255, 255, 0.904);
    border-bottom: 1px solid rgba(187, 187, 187, 0.37);
    box-shadow: 0px 10px 14px -7px #00000086;
    background-color: #0d3881d8;
    backdrop-filter: saturate(180%) blur(20px);
    right: 0;
    position: fixed;
    z-index: 1000;
    font-size: 14px;
    transition: all 0.2s;
}

.navbar a {
    color: white;
    text-decoration: none;
}

.navbar .white a {
    color: black;
}

.navbar .nav-button {
    padding: 10px;
    margin: 5px;
    border-radius: 3px;
    display: flex;
    align-items: center;
    text-align: center;
    border: 1px solid rgba(0, 0, 0, 0.425);
    box-shadow: 0px 10px 14px -7px #00000086;
    font-weight: bold;
}

.navbar .nav-button.ml-auto {
    margin-left: auto;
    margin-right: 30px;
}

.navbar .nav-button > span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.2s;
}

.navbar .nav-button:active {
    transform: translateY(1px) translateX(1px);
}

.navbar .nav-button.active:active {
    transform: translateY(6px) translateX(1px);
}

.navbar .nav-button:hover {
    filter: brightness(90%);
}

.navbar .nav-button.active {
    padding-bottom: 20px;
    border-radius: 3px 3px 0px 0px;
    transform: translateY(5px);
    box-shadow: 0px 10px 14px -7px #00000015;
    border-bottom: 0px;
}

@media screen and (max-width: 980px) {
    .nav-buttons {
        margin-left: auto;
        height: 45vh;
        width: 25%;
        flex-direction: column;
        align-items: flex-end;
        top: 60px;
        right: 0px;
        position: fixed;
        display: none;
        border: none;
        box-shadow: none;
        backdrop-filter: none;
        background-color: transparent;
    }

    .navbar .nav-button.active {
        padding: 10px;
        padding-right: 40px;
        border-radius: 3px;
        transform: translateX(-30px);
    }

    .navbar .nav-button.active:active {
        transform: translateY(1px) translateX(-29px);
    }

    .nav-buttons.open {
        display: inherit;
    }

    .nav-buttons > .nav-button {
        width: 150px;
    }

    .navbar > .nav-buttons .nav-button.ml-auto {
        margin-left: 5px;
        margin-right: 5px;
    }

    .line {
        width: 30px;
        height: 3px;
        background: white;
        margin: 5px;
    }

    .light .line {
        background: black;
    }

    .hamburger {
        position: fixed;
        cursor: pointer;
        z-index: 50;
        top: 30px;
        right: 5%;
        transform: translate(-5%, -50%);
    }
}

@media screen and (max-width: 480px) {
    .nav-buttons {
        width: 40%;
        border: 0px;
    }
}
